class viewPortFix {
  setMainHight = async (arg) => {
    const tmp = await import("./viewPortFix.js");
    return tmp.setMainHight(arg);
  };
}

const fix = new viewPortFix();

// fix.setMainHight('--main-height',parseFloat(getComputedStyles(document.querySelector('header'))['height'])

window.addEventListener("resize", () => {
  //   console.log(tmp);
  fix.setMainHight([
    "--main-height",
    parseFloat(
      window.getComputedStyle(document.querySelector("header"))["height"]
    ),
  ]);
});

document.addEventListener("DOMContentLoaded", () => {
  fix.setMainHight([
    "--main-height",
    parseFloat(
      window.getComputedStyle(document.querySelector("header"))["height"]
    ),
  ]);
});
